import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';


import { NetworkProvider } from '../../providers/network/network';

import { SocialSharing } from '@ionic-native/social-sharing';

/**
 * Generated class for the DetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detail',
  templateUrl: 'detail.html',
})
export class DetailPage {

  parameter1: string;
  parameter2: string;
  parameter3: string;
  parameter4: string;
  parameter5: string;
  parameter6: string;
  parameter7: string;
  parameter8: string;


  data:Detail[];

  title = "test";
  description = "xxxyyyzzz";
  recipeUrl = "yyy";

  

  constructor(public navCtrl: NavController,
   public navParams: NavParams,
   public net : NetworkProvider,
   public socialSharing: SocialSharing) {

    this.parameter1 = navParams.get('param1');
    this.parameter2 = navParams.get('param2');
    this.parameter3 = navParams.get('param3');
    this.parameter4 = navParams.get('param4');
    this.parameter5 = navParams.get('param5');
    this.parameter6 = navParams.get('param6');
    this.parameter7 = navParams.get('param7');
    this.parameter8 = navParams.get('param8');

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetailPage');
    this.display();
  }


  share() {
  this.socialSharing.shareWithOptions({
    message: `${this.title} - ${this.description}: ${this.recipeUrl}`
  }).then(() => {
    console.log('Shared!');
  }).catch((err) => {
    console.log('Oops, something went wrong:', err);
  });
}


  display()
  {

    this.net.getPromise().subscribe((data) =>
      {
        this.data = data[0]['Player_name'];
        setTimeout(() => console.log(this.data), 2000);
      });
  }

}


interface Detail
{
  pol: string,
  pod: string,
  size: number,
  commodity_type: string,
  company_name: string,
  location: string,
  mobile_num: string,
  email: string

}




